// ----------------------------------------*/
.l-header {
  top: 0; /*  0にしないと上に隙間ができる */
  left: 0;
  right: 0;
  margin: 0;
  width: 100%;
  z-index: 500;
  position: relative;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: transparent;
  transition: opacity 0.2s;
