// ボディにヘッダーの幅分マージン入れる
// --------------------------------------------*/
body[data-float=true] {
  /*      ヘッダーが透過せず、TOPイメージがヘッダーの下から始まっている場合*/
  /*      ヘッダーが最初から透過、TOPイメージがY0から始まっている場合*/
